import Item from "../components/Item.vue"
Vue.component("Item",Item)

//全局混入
Vue.mixin({
  data(){
    return {
      msg:'这是全局混入',
      common:{}
    }
  },
  created(){
    console.log(this.msg);
    
  },
  methods:{
    getCommon(types){
      this.$axios.get("vue/index").then(res=>{
        this.common = res.data+types;
        console.log(this.common);
      })
    }
  }
})