<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- <p>{{goods[0].discount}}</p> -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <Item v-for="(item,index) in list" :key="index" :good="item"></Item>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  props: ["goods"],
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      isLoading: false
    };
  },
  watch: {
    goods: function(value) {
      //?? 数据下拉刷新后 this.finished = true;
      this.list = [];
         var len = this.list.length;
        for (let i = len; i < 10 + len; i++) {
          this.list.push(this.goods[i]);
        }
    }
  },
  mounted() {   
    console.log(this.goods); //所有的数据  数组
  },
  methods: {
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        var len = this.list.length;
        for (let i = len; i < 10 + len; i++) {
          this.list.push(this.goods[i]);
        }
        // console.log(this.list)
        this.loading = false;
        // 数据全部加载完成
        if (this.list.length >= this.goods.length) {
          this.finished = true;
        }
      }, 600);
      // this.$axios.get("/vue/getGoods", { params: { limit: 10 } }).then(res => {
      //   console.log(this.goodList);
      //   this.goodList= this.goodList.concat(res.data.result);
      //   this.loading = false;
      //   // 数据全部加载完成
      //   if (this.goodList.length >= 40) {
      //     this.finished = true;
      //   }
      // });
    },
    onRefresh() {
      console.log("开始 刷新..." + this.isLoading);
      setTimeout(() => {
        this.isLoading = false;
        this.$toast("下拉刷新成功");
        // 自定义事件
        this.$emit("changeallgoods");
      }, 1000);
    }
  }
};
</script>