import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

// @  是别名  代表 /src/
import Guide from "@/views/Guide.vue";
const routes = [
  {
    path: "/",
    redirect: {
      name: "guide"
    }
  },
  {
    path: "/guide",
    name: "guide",
    component: Guide
  },
  {
    path: "/search",
    name: "search",
    component: () => import("@/views/Search.vue")
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/Login.vue")
  },
  {
    path: "/good/:goodId",
    name: "good",
    component: () => import("@/views/Good.vue")
  },
  {
    path: "/index",
    name: "index",
    component: () => import("@/views/Index.vue"),  //路由懒加载
    children: [
      {
        path:"",
        redirect:{name:"home"}
      },
      {
        path: "home",
        name: "home",
        component: () => import("@/views/Home.vue")
      },
      {
        path: "classify",
        name: "classify",
        component: () => import("@/views/Classify.vue")
      },
      {
        path: "cart",
        name: "cart",
        component: () => import("@/views/Cart.vue")
      },
      {
        path: "mine",
        name: "mine",
        component: () => import("@/views/Mine.vue")
      }
    ]
  }
]

const router = new VueRouter({
  routes,
  mode: "hash",
  base: "/"  //可忽略
})

export default router  //暴露



