//vuex
import Vue from 'vue'
import Vuex from 'vuex'
// import { url } from 'inspector';
import axios from "@/utils/axios";
Vue.use(Vuex)

const moduleCar = {
  state:{
    carNum:0
  },
  actions:{

  },
  mutations:{

  },
  getters:{

  }
}

const moduleData = {
  state:{
    banners:[]
  },
  actions:{
    getBannerList({commit},{url,params}){
      axios.get(url,{params}).then(res=>{
        commit('getBannerList',res.data.result);
      })
    }
  },
  mutations:{
    getBannerList(state,banners){
      state.banners = banners;
    }
  }
}

export default new Vuex.Store({
  modules:{
    cars:moduleCar,
    data:moduleData
  },
  state:{
    count:2019,
    city:'武汉',
    goodList: [],
    typeList: [],
    searchList: [],
    keyword:'',
    userInfo:{}
  },
  mutations:{
    addCount(state, num){
      state.count+=num;
    },
    changeCity(state, city){
      state.city = city;
    },
    getGoodList(state,goodList){
      state.goodList = goodList;
    },
    getTypeList(state,typeList){
      state.typeList = typeList;
    },
    getSearchList(state,searchList){
      state.searchList = searchList;
    },
    getKeyword(state,keyword){
      state.keyword = keyword;
    },
    changeUserInfo(state,userInfo){
      // Mutation 需遵守 Vue 的响应规则  新对象替换老对象  
      // state.userInfo = { username : "aaaaa"}  
      // state.userInfo = [...state.userInfo,...userInfo]
      state.userInfo = {...state.userInfo,...userInfo};
    }
  },
  actions:{
    getTypeList({commit},{url,cb}){
      axios.get(url).then(res => {
        cb();
        commit('getTypeList',res.data.result);
      });    
    },
    getSearchList({commit},{url,params}){
      axios.get(url,{params}).then(res=>{
        commit("getSearchList", res.data.result)
      })
    }
  },
  getters:{
    //一般用来对store.state数据进行优化  类似于computed的get用法
    newCount(state){
      return state.count*10
    }
  }
})

