<template>
  <van-nav-bar class="head" :title="title" :left-text="show?'返回':''" :left-arrow="show" @click-left="onClickLeft">
    <van-icon name="search" slot="right" v-if="showSearch" @click="goSearch" />
  </van-nav-bar>
</template>
<script>
export default {
  data(){
    return {
      showSearch: true
    }
  },
  props:{
    title:{
      type: String,
      default:"标题"
    },
    show:{
      type:Boolean,
      default: false
    }
  },
  mounted(){
    if(this.$route.name == "search"){
      this.showSearch = false;
    }
  },
  methods:{
    onClickLeft(){
      //返回   
      this.$router.go(-1);
    },
    goSearch(){
      //点击搜索图标跳转到搜索页
      this.$router.push({name:"search"})
    }
  }
}
</script>